var NEProduct = nemReact.require("NEProduct", "react");
var Loading = require('../common/Loading.react');
var AdvEventAction = nemReact.require('AdvEventAction');
var AdvEventStore = nemReact.require('AdvEventStore');
var NetwTools = nemReact.require('NetwTools');

function getHotSales() {
    var hotSales = AdvEventStore.getHotSales();
    if (typeof hotSales === 'undefined' || Object.keys(hotSales).length === 0) {
        AdvEventAction.getHotSales();
    }
    return {hotSales: hotSales};
}

var HotSalesRadio = React.createClass({
    render: function() {
        return (<input type={"radio"} name={"hotSaleTab"} id={"hotSaleTab0" + this.props.order.toString()} hidden={true} defaultChecked={this.props.order == 1
            ? true
            : false}/>);
    }
});

var HotSalesLabel = React.createClass({

    showSixTab: function(order) {
        var name = "";
        switch (order) {
            case 1:
                name = "國際風尚";
                break;
            case 2:
                name = "品味生活";
                break;
            case 3:
                name = "活力健康";
                break;
            case 4:
                name = "時尚科技";
                break;
            case 5:
                name = "樂活時尚";
                break;
            case 6:
                name = "海外直購";
                break;
            default:
                name = "熱銷" + index;
        }
        return name;
    },

    render: function() {
        return (
            <label htmlFor={"hotSaleTab0" + this.props.order} onClick={this.props.setCurrentTab}>
                <span>{this.showSixTab(this.props.order)}</span>
            </label>
        );
    }
});

var HotSalesTabProduct = React.createClass({
    render: function() {
        return (
            <li className={"hotSaleTop0" + this.props.currentTab}>
                <h3>{this.props.hotSales.advEventType.AdvTypeName}</h3>
                <ul className="prodLists ranks">
                    {this.props.hotSales.advEventList.map(function(advEvent, index) {
                        return (
                            <li key={index} className={"top" + (index > 8
                                ? "" + (index + 1)
                                : "0" + (index + 1))}>
                                <NEProduct
                                  key={index}
                                  productID={advEvent.ItemID}
                                  imgUrl={NetwTools.isHttpUriAbsolute(advEvent.ImgUrl) ? advEvent.ImgUrl : _netwImageSSLDM + advEvent.ImgUrl}
                                  productLink={advEvent.LinkUrl}
                                  title={advEvent.Title}
                                  qty={advEvent.ItemStock}
                                  marketPrice={advEvent.MarketPrice}
                                  sellingPrice={advEvent.SalePrice}/>
                            </li>
                        )
                    }, this)
}
                </ul>
            </li>
        );
    }
});

var HomeTabThree = React.createClass({

    showRadioInput: function() {
        var radios = []
        for (var i = 1; i <= Object.keys(this.state.hotSales).length; i++) {
            var singleRadio = <HotSalesRadio key={i} order={i}/>;
            radios.push(singleRadio);
        }
        return radios;
    },
    showLabelForRadio: function() {
        var labels = []
        for (var i = 1; i <= Object.keys(this.state.hotSales).length; i++) {
            var label = <HotSalesLabel key={i} order={i} name={this.state.hotSales[i.toString()].advEventType.AdvTypeName} setCurrentTab={this.setCurrentTab.bind(this, i)}/>;
            labels.push(label);
        }
        return labels;
    },

    showLoad: function() {
        if (this.state.isLoad) {
            return <Loading/>;
        } else {
            return null;
        }

    },

    setCurrentTab: function(tabNumber) {
        this.setState({currentTab: tabNumber});
    },

    showTabProduct: function() {
        var tabProducts;
        var currentTab = this.state.currentTab.toString();
        if (this.state.hotSales.hasOwnProperty(currentTab)) {
            tabProducts = <HotSalesTabProduct currentTab={this.state.currentTab} hotSales={this.state.hotSales[currentTab]}/>;
        }
        return tabProducts;
    },

    getInitialState: function() {
        return {hotSales: {}, currentTab: 1, isLoad: true};
    },

    componentWillMount: function() {
        var hotSales = getHotSales();
        if (Object.keys(hotSales.hotSales).length > 0) {
            this.setState({isLoad: false});
        }
        this.setState(hotSales);
    },

    componentDidMount: function() {
        AdvEventStore.addChangeListener(this._onChange);
    },

    componentWillUnmount: function() {
        AdvEventStore.removeChangeListener(this._onChange);
    },

    render: function() {
        return (
            <li className={"current"} style={{
                display: "block"
            }}>
                <div className="hotSaleTop">
                    {this.showRadioInput()}
                    <div className="hotSaleTabs">
                        {this.showLabelForRadio()}
                    </div>
                    <ul className="hotSaleTopContent">
                        {this.showTabProduct()}
                    </ul>
                </div>
                {this.showLoad()}
            </li>
        );
    },

    _onChange: function() {
        this.setState({isLoad: false});
        this.setState(getHotSales());
    }
});

module.exports = HomeTabThree;



