var HomeTabThree = require('./components/home/HomeTabThree.react');

var init = function(elementID) {
    ReactDOM.render(
        <HomeTabThree/>, document.getElementById(elementID));
};

var getComponent = function(){
    return <HomeTabThree />;
};

window.nemReact.reacts['homeHotSale'] = {
    init: init,
    getComponent: getComponent
};



